import React from 'react'

export const WeatherBlock = ({ weather }) => {
    let iconUrl = `http://openweathermap.org/img/wn/${weather.weather[0].icon}.png`;
    return (
        <>
            <hr />
            <li><span className="weather-time">{new Date(weather.dt_txt).getHours()}:00</span>
                <span className="weather-temp">{Math.round(weather.main.temp)}째C</span>
                <img src={iconUrl} alt="" />
                <span className="weather-desc">{weather.weather[0].description}</span>
            </li>
        </>
    )
}
